import React, { Component } from 'react';
import { Row, Col, Icon, Input, notification, Button } from 'antd';

const Search = Input.Search;
class Chatbox extends Component {
    render() {
        return (
            <Row style={{ width: 300, padding: 5, backgroundColor: '#4080ff' }} type="flex" justify="space-between">
                <Col>
                    <span style={styles.online}></span>An Lan Can
                </Col>
                <Col>
                    <Icon style={styles.icon} type="plus" />
                    <Icon style={styles.icon} type="phone" />
                    <Icon style={styles.icon} type="video-camera" />
                    <Icon style={styles.icon} type="setting" />
                    <Icon style={styles.icon} type="close" />
                </Col>
                <Row style={{ backgroundColor: "white", width: 300, height: 300 }}></Row>
                <Row type="flex" justify="space-between" align="center" style={{width:300,padding:7}}>
                    <Col style={{display:'flex',justifyContent:'center',alignItems:'center'}}>
                        <Icon style={styles.icon} type="appstore-o" />
                    </Col>
                    <Col style={{display:'flex',justifyContent:'center',alignItems:'center'}}>
                        
                    </Col>
                    <Col style={{display:'flex',justifyContent:'center',alignItems:'center'}}>
                        <Icon style={styles.icon} type="like-o" />
                    </Col>
                </Row>
            </Row>
        );
    }
}
const styles = {
    online:
    {
        background: '#42b72a',
        borderRadius: '50%',
        height: 6,
        display:'inline-block',
        marginLeft: 3,
        marginRight: 6,
        marginBottom:1,
        verticalAlign: 'middle',
        width: 6
    },
    icon:{ 
        padding:5,
        fontSize: 16,
        verticalAlign: 'middle',
        display:'inline-block',
    }
}

export default Chatbox;


